import React, { useState, useEffect } from 'react';
import { BdsSelect } from 'blip-ds/dist/blip-ds-react';
import Select from '../Select/index'
import getMessageTemplate from '../../services/getMessageTemplate'
import guid from '../../utils/guid';
import { showToast } from "../../api/commomService";
import getNameSpaceWhatsApp from '../../services/getNameSpaceWhatsApp';
import getStateBot from '../../services/getStateBot';
import getTokenTray from '../../services/getTokenTray';
import getStatuses from '../../services/getOrderStatudes';
import { consumer_key, consumer_secret } from '../../constants/application-actions';
import setServiceTray from '../../services/setServiceTray';
import configService from '../../services/getServiceTray';

const SendNotification = ({ shortName, accessKey1, email }) => {


  const [config, setConfig] = useState({ value: "No message selected", config: "" });
  const [data, setData] = useState([]);
  const [dataSubBot, setDataSubBot] = useState([]);
  const [dataLanguage, Language] = useState([{ label: 'Português', value: 'pt_BR' }, { label: 'Inglês', value: 'en_US' }]);
  const [message, setMessage] = useState([])
  const [template, setTemplate] = useState('');
  const [stateId, setStateId] = useState('');
  const [flowId, setFlowId] = useState('');
  const [filterLanguage, setFilterLanguage] = useState('');
  const [inputList, setInputList] = useState([{ state: "", statusName: "", template: "", variables: [], }]);
  const [loading, setLoading] = useState(false);
  const [phone, setPhone] = useState('');
  const [templateName, setTemplateName] = useState('Select a template');
  const [language, setLanguage] = useState('Select a language');
  const [subBots, setSubBots] = useState(JSON.parse(localStorage.getItem('details')));
  const [templateSubBot, setTemplateSubBot] = useState('Select a template');
  const [identity, setIdentity] = useState('');
  const [dataFlowId, setDataFlowId] = useState([{ label: "No message selected", value: "dfdd" }]);
  const [templateSubBotFlowId, setTemplateSubBotFlowId] = useState('Select a state');
  const [inputValueList, setInputValueList] = useState("");
  const [showStatus, setShowStatus] = useState(false);
  const [statusTray, setStatusTray] = useState(false);  
  const [statusTrayId, setStatusTrayId] = useState(false);
  const [dataVarieble, setDataVariable] = useState([]);
  const [variableLength, setVariableLength] = useState("");
  const [listMessageTemplate, setListMessageTemplate] = useState([]);
  const [modalDeleteState, setModalDeleteState] = useState(false)
  const [textDeleteState, setTextDeleteState] = useState("")

  const removerCaracteresEspeciais = (string) => {
    // Remove caracteres especiais usando expressão regular
    var stringSemCaracteresEspeciais = string.replace(/[^\w\s]/gi, '');

    // Converte a string para caixa baixa
    var stringEmCaixaBaixa = stringSemCaracteresEspeciais.toLowerCase();

    return stringEmCaixaBaixa;
  }

  const fnAuxTemplates = async () => {

    const arrayTemplates = [];



    const response = await getMessageTemplate(guid(), localStorage.getItem('info'));

    const resultNameSpace = await getNameSpaceWhatsApp(guid(), localStorage.getItem('info'));

    // resultNameSpace.data?.resource?.name
    const resultConfig = await configService(removerCaracteresEspeciais(resultNameSpace.data?.resource?.name));
    setListMessageTemplate(resultConfig.data.data.messageTemplate || []);
    localStorage.setItem('tray-token', resultConfig.data.data.tokenCart);


    const templates = response.data.resource.data.filter((t) => t.name && t.language === filterLanguage && t.name.split('_')[0] !== 'sample' && t.status === 'APPROVED');

    templates.map((template) => {
      arrayTemplates.push({ id: template.name, label: template.name, value: template.name, selected: false })
    });


    setData(arrayTemplates);
    setMessage(templates);


  }

  const fnAuxStatusTray = async () => {

    const url = localStorage.getItem('plugin-tray-key2');
    const code = localStorage.getItem('plugin-tray-key');

    const arrayStatusTray = []

    const { data: { access_token } } = await getTokenTray(url, consumer_key, consumer_secret, code);

    const { data } = await getStatuses(url, access_token);

    data.OrderStatuses.map((e) => {

      arrayStatusTray.push({ id: e.OrderStatus.id, label: e.OrderStatus.status, value: e.OrderStatus.id, selected: false })

    })

    setStatusTray(arrayStatusTray);

  }


  const fnAuxSubBots = () => {

    const arraySubBots = [];
    subBots.map((bots) => { arraySubBots.push({ id: bots.longName, label: bots.longName, value: bots.longName, selected: false }); })
    setDataSubBot(arraySubBots);

  }




  useEffect(() => {

    const url = localStorage.getItem('plugin-tray-key2');
    const code = localStorage.getItem('plugin-tray-key');

    if (code !== '' && url !== '') {

      fnAuxStatusTray()
    }

    fnAuxTemplates()

  }, [filterLanguage]);



  useEffect(() => {

    fnAuxSubBots()

  }, []);


  const handleInputChange = (e, index) => {

    const list = [...inputList];

    list[index].template = e;
    setInputList(list);
  };
  const handleInputChangeStatus = (e, index) => {

    const list = [...inputList];

    const status = statusTray.filter((x) => x.id === e)


    list[index].state = e;
    list[index].statusName = status[0].label;
    setInputList(list);
  };


  const handleRemoveClick = index => {

    const list = [...inputList];
    list.splice(index, 1);
    setInputList(list);
  };


  const handleAddClick = () => {

    if (inputList.length >= 10) {
      showMessage('Não é possivel incluir mais status!', 'warning', 'Notice!');
      return
    }
    setInputList([...inputList, {
      state: "",
      template: ""
    }
    ]);
  };


  const handleSendMessage = async () => {
    setLoading(true);


    if (!localStorage.getItem("plugin-tray-key")) {
      showMessage('O campo Code precisa ser preenchido!', 'danger', 'Notice!');
      redirectFirstItem()
      return
    }

    if (!localStorage.getItem("plugin-tray-key2")) {
      showMessage('O campo URL precisa ser preenchido!', 'danger', 'Notice!');
      redirectFirstItem()
      return
    }

    if (!localStorage.getItem("plugin-tray-key3")) {
      showMessage('O campo Id da loja precisa ser preenchido!', 'danger', 'Notice!');
      redirectFirstItem()
      return;
    }

    if (!filterLanguage) {
      showMessage('O campo idioma precisa ser preenchido!', 'warning');
      return
    }
    if (!identity) {
      showMessage('O campo Chatbot precisa ser preenchido!', 'warning');
      return
    }
    if (!stateId) {
      showMessage('O campo Bloco precisa ser preenchido!', 'warning');
      return
    }

    const resultNameSpace = await getNameSpaceWhatsApp(guid(), localStorage.getItem('info'));

    let app = removerCaracteresEspeciais(resultNameSpace.data.resource.name)

    const payload = {
      flowId: flowId,
      masterState: identity,
      stateId: stateId,
      languageCode: filterLanguage,
      namespace: resultNameSpace.data.resource['message_template_namespace'],
      trackOrigin: true,
      checkAttendance: true,
      senderEmail: email,
      messageTemplate: inputList,
      accessKey1,
      shortName,
      url: localStorage.getItem("plugin-tray-key2"),
      code: localStorage.getItem("plugin-tray-key"),
      storeId: localStorage.getItem("plugin-tray-key3"),
      company: app,
      apiKey: 'Key ' + btoa(`${shortName}:` + atob(accessKey1)),
      consumer_key,
      consumer_secret
    }


    console.log(payload)

    const { status } = await setServiceTray(payload);


    if (status === 409) {

      showMessage('Essa configuração já existe', 'warning');
      return
    }

    showMessage('Configuração salva com sucesso', 'success');

    setTimeout(() => {
      fnAuxTemplates()
    }, 3000);
  }

  const redirectFirstItem = () => {

    setTimeout(() => {
      document.querySelector("a").click()
    }, 2000);

  }

  const showMessage = (msg, type) => {

    setTimeout(() => {

      showToast({
        type: type,
        message: msg
      })
      setLoading(false);

    }, 900);

  }

  const handleSetEventLanguage = (event) => {

    setFilterLanguage(event);
    setLanguage(event);

  }


  const handleSetEventSubBot = async (event) => {

    const nameSubBot = event
    setTemplateSubBot(nameSubBot);
    const arrayVariable = [];

    const { identifier, accessKey, identity } = subBots.filter((e) => e.longName === nameSubBot)[0];
    const KEY = 'Key ' + btoa(`${identifier}:` + atob(accessKey));
    const resultHttp = await getStateBot(localStorage.getItem('info'), identifier, accessKey);
    const data = resultHttp.data.statesInformation;

    let arrayStates = [];

    for (const iterator of data) {

      arrayStates.push({ id: iterator.name, label: iterator.name, value: iterator.id, selected: false })

    }

    setDataFlowId(arrayStates);
    setFlowId(resultHttp.data.flowId);
    setIdentity(identity);


    ["name", "estimated_delivery_date", "tracking_url", "orderId"].map((e) => {
      arrayVariable.push({ id: e, label: e, value: e, selected: false })
    });


    setDataVariable(arrayVariable)


  }
  const handleSetEventSubBotFlowId = async (event) => {

    const url = localStorage.getItem('plugin-tray-key2');
    const code = localStorage.getItem('plugin-tray-key');

    if (code !== '' && url !== '') {
      setTemplateSubBotFlowId(event);
      setStateId(event);
      setShowStatus(true)
    } else {

      showMessage('Você precisa fazer a configuração da tray', 'warning');
      redirectFirstItem();
    }

  }

  const handleInputChangeVariable = (e, index, value) => {

    console.log(e, index)

    try {

      if (e.length > inputList[index].value) {

        showMessage(`Você só pode incluir ${inputList[index].value == 1 ? `${inputList[index].value} variável para esse template` :
          `${inputList[index].value} variáveis para esse template`} `, 'danger', 'Notice!');

      } else {

        const list = inputList

        list[0].variables = [];

        list[0].variables = e;

        setInputList(list);

      }

    } catch (e) {
      return [];
    }

  };

  const openModalDeleteState = (state, stateId) => {

    setModalDeleteState(true);
    setStatusTrayId(stateId);
    setTextDeleteState(state);

  }

  const deleteMessageTempteByStatus = (id) => {
    const contract = localStorage.getItem('tenantId');
    const url = `https://pontoparse.herokuapp.com/api/v1/api-parse-tray/plugin/${contract}/state/${id}`;
    //const url = `http://localhost:3333/api/v1/api-parse-tray/plugin/${contract}/state/${id}`;

    fetch(url, {
      method: 'DELETE',
      headers: {
        'Content-Type': 'application/json',
      },
    })
      .then(response => {
        if (!response.ok) {
          showMessage('Erro ao remover o item', 'danger');
          setModalDeleteState(false)
          throw new Error(`Erro na solicitação: ${response.status}`);

        }
        return response.json();
      })
      .then(data => {
        fnAuxTemplates()
        showMessage('Item removido com sucesso!', 'success');
        setModalDeleteState(false)
        console.log('Resposta:', data);
      })
      .catch(error => {
        console.error('Erro:', error.message);
        showMessage('Erro ao remover o item', 'danger');
        setModalDeleteState(false)
      });

  }

  return (

    <>
      {listMessageTemplate.length > 0 && <>
        <div style={{ display: 'flex', justifyContent: 'center' }}>
          <div >
            <label style={{ fontSize: '18px' }}>Lista de status cadastrados!</label>
          </div>

        </div>
      </>}
      {listMessageTemplate.length > 0 && <>  <div style={{ width: '100%' }}>
        <bds-paper>
          <div class="rounded-lg bg-white pt-6 pb-2">
            <div class="max-h-80 px-4 scrollbar-thin scrollbar-track-transparent scrollbar-thumb-zinc-300">
              <table class="w-full table-auto "><thead>
                <tr class="sticky top-0 z-10 border-b border-neutral-medium-silver bg-white text-base text-black">
                  <th class="py-2 font-semibold">Status</th>
                  <th class="py-2 font-semibold">Template</th>
                  <th class="py-2 font-semibold">Variaveis</th>
                </tr></thead>
                <tbody >
                  {listMessageTemplate.map((x, i) => {
                    return (
                      <>
                        <tr key={x.state}>
                          <td class="py-2" >{x.statusName}</td>
                          <td class="py-2" >{x.template}</td>
                          <td class="py-2" >{x.variables.join(",")}</td>
                          <td class="py-2" ><bds-icon name="trash" color="red" style={{ marginTop: '5%', cursor: 'pointer' }} role="img" onClick={() => openModalDeleteState(x.statusName, x.state)} class="bds-icon bds-icon__size--medium hydrated" aria-label="trash" theme="outline"></bds-icon>
                          </td>
                        </tr>
                      </>
                    )
                  })}
                </tbody>
              </table>
            </div>
          </div>
        </bds-paper>
      </div>
        <hr />
      </>}

      <div style={{ display: 'flex', justifyContent: 'center' }}>
        <div >
          <label style={{ fontSize: '18px' }}>{listMessageTemplate.length === 0 ? 'Cadastre seus status!' : 'Atualize seus status!'} </label>
        </div>
      </div>
      <div className="container w-100">
        <div className="row" style={{ display: 'flex', justifyContent: 'center' }}>


          <div className="col-3">
            <label>Idoma: </label>
            <Select
              icon="search"
              name="template"
              label="Selecione um idioma"
              placeholder="Selecione"
              multiple={false}
              searchable={true}
              options={dataLanguage}
              selected={dataLanguage}
              onChange={($event) => handleSetEventLanguage($event)}
            />
          </div>
          <div className="col-3">
            <div className="col-3">
              <label>Chatbot:</label>
            </div>
            <Select
              icon="search"
              name="template"
              label="Selecione um Chatbot"
              placeholder="Selecione"
              multiple={false}
              searchable={true}
              options={dataSubBot}
              selected={dataSubBot}
              onChange={($event) => handleSetEventSubBot($event)}
            />
          </div>
          <div className="col-3">
            <div className="col-3">
              <label>Bloco:</label>
            </div>
            <Select
              icon="search"
              name="template"
              label="selecione um Bloco"
              placeholder="Selecione"
              multiple={false}
              searchable={true}
              options={dataFlowId}
              selected={dataFlowId}
              onChange={($event) => handleSetEventSubBotFlowId($event)}
            />
          </div>
          <hr />
          {showStatus && <div className="container w-100" >
            <hr />

            <div className="row" style={{ display: 'flex', justifyContent: 'center' }}>
              <div className="col-12">
                <label>Status Tray:</label>
              </div>
              <div className="col-12">

              </div>
            </div>

            {inputList.map((x, i) => {
              return (
                <div className="mt-2 mb-2" key={i.toString()}>
                  <div class="row" style={{ display: 'flex', justifyContent: 'center' }}>
                    <div class="col-3">
                      <label>Status- {i + 1}:</label>
                      <Select
                        icon="filter"
                        name="state"
                        label="Selecione um status"
                        placeholder="Selecione"
                        multiple={false}
                        searchable={true}
                        options={statusTray}
                        selected={statusTray}
                        onChange={($event) => handleInputChangeStatus($event, i)}
                      />
                    </div>
                    <div class="col-4">
                      <label>Template - {i + 1}:</label>
                      <Select
                        icon="filter"
                        name="template"
                        label="Selecione um modelo de mensagem"
                        placeholder="Selecione"
                        multiple={false}
                        searchable={true}
                        options={data}
                        selected={data}
                        onChange={($event) => handleInputChange($event, i)}
                      />
                    </div>
                    <div class="col-4">
                      <label>Variável - {i + 1}:</label>
                      <Select
                        icon="filter"
                        name="Variável"
                        label={`Escolha  variáveis para seu template`}
                        placeholder="Selecione"
                        multiple={true}
                        searchable={true}
                        options={dataVarieble}
                        selected={dataVarieble}
                        onChange={($event) => handleInputChangeVariable($event, i, variableLength)}
                      />
                    </div>
                    <div class="col-1">
                      <bds-icon name="trash" color="red" style={{ marginTop: '75%', cursor: 'pointer' }} onClick={() => handleRemoveClick(i)} role="img" class="bds-icon bds-icon__size--medium hydrated" aria-label="trash" theme="outline"></bds-icon>

                    </div>
                  </div>
                  <hr />
                </div>
              );
            })}


            <div style={{ display: 'flex', justifyContent: 'space-between' }}>


              {inputList.length < 10 && <bds-button icon="plus" style={{ width: '45%' }} size="short" onClick={handleAddClick} >Adicionar Status</bds-button>}

              {inputList.length > 0 && <bds-button icon="save-disk" size="short" onClick={handleSendMessage} style={{ width: inputList.length < 5 ? '45%' : '100%' }} >Salvar configuração</bds-button>}

            </div>



          </div>}

        </div>
      </div>

      <bds-alert open={modalDeleteState}>
        <bds-alert-header variant="error" icon="error">
          Atenção!
        </bds-alert-header>
        <bds-alert-body>
          Você realmente deseja excluir o status: <strong>{textDeleteState}</strong>? Esse processo é <strong>irreversível</strong>.

        </bds-alert-body>
        <bds-alert-actions>
          <bds-button icon="refresh" variant="secondary" onClick={() => deleteMessageTempteByStatus(statusTrayId)}>Sim</bds-button>
          <bds-button icon="close" variant="secondary" onClick={() => setModalDeleteState(false)}>Não</bds-button>

        </bds-alert-actions>
      </bds-alert>

    </>


  )

};

export default SendNotification;