import React, { useState } from 'react';


//"orderId", "statusDescription", "trackingUrl", "shippingEstimateDate", "fullName", "codeBar", "urlPayments"

function DicionarioDeDados() {


  return (

    <>
    <bds-accordion-group collapse='single'>
    <bds-accordion>
        <bds-accordion-header accordion-title="orderId" icon="" avatar-name=""
          avatar-thumb="">
        </bds-accordion-header>
        <bds-accordion-body>
          <bds-typo variant="fs-16">
           OrderId = Essa variável contém o id do pedido do cliente.
          </bds-typo>
        </bds-accordion-body>
        
      </bds-accordion>

      <bds-accordion>
        <bds-accordion-header accordion-title="estimated_delivery_date" icon="" avatar-name=""
          avatar-thumb="">
        </bds-accordion-header>
        <bds-accordion-body>
          <bds-typo variant="fs-16">
          estimated_delivery_date = Essa variável contém a data de estimativa de entrega do pedido.
          </bds-typo>
        </bds-accordion-body>
        
      </bds-accordion>


      <bds-accordion>
        <bds-accordion-header accordion-title="Name" icon="" avatar-name=""
          avatar-thumb="">
        </bds-accordion-header>
        <bds-accordion-body>
          <bds-typo variant="fs-16">
          name = Essa variável contém o nome do cliente.
          </bds-typo>
        </bds-accordion-body>
        
      </bds-accordion>



      <bds-accordion>
        <bds-accordion-header accordion-title="tracking_url" icon="" avatar-name=""
          avatar-thumb="">
        </bds-accordion-header>
        <bds-accordion-body>
          <bds-typo variant="fs-16">
          tracking_url = Essa variável contém a url de rastreio do pedido do cliente.
          </bds-typo>
        </bds-accordion-body>
        
      </bds-accordion>

    </bds-accordion-group>
    
    </>
  );

}


export default DicionarioDeDados;