import React, { useState, useEffect } from "react";

import { BdsInputPassword, BdsInput } from 'blip-ds/dist/blip-ds-react';
import { showToast } from "../../api/commomService";
import deleteConfigService from '../../services/deleteConfigTray';
import configService from '../../services/getServiceTray';
import Switch from "react-switch";
import getNameSpaceWhatsApp from '../../services/getNameSpaceWhatsApp';
import guid from '../../utils/guid';


import "./index.css"


const Config = () => {
    
    const [key, setKey] = useState(localStorage.getItem('plugin-tray-key'));
    const [key2, setKey2] = useState(localStorage.getItem('plugin-tray-key2'));
    const [key3, setKey3] = useState(localStorage.getItem('plugin-tray-key3'));
    const [showModal, setShowModal] = useState(false)
    const [checkedServices, setCheckedServices] = useState(false);
    const [company, setCompany] = useState("")

    
    function removerCaracteresEspeciais(string) {
        // Remove caracteres especiais usando expressão regular
        var stringSemCaracteresEspeciais = string.replace(/[^\w\s]/gi, '');
        
        // Converte a string para caixa baixa
        var stringEmCaixaBaixa = stringSemCaracteresEspeciais.toLowerCase();
        
        return stringEmCaixaBaixa;
      }
      


    useEffect(async () => {

        const resultNameSpace = await getNameSpaceWhatsApp(guid(), localStorage.getItem('info'));
        
        setCompany(removerCaracteresEspeciais(resultNameSpace.data.resource.name));
        const resultConfig = await configService(removerCaracteresEspeciais(resultNameSpace.data.resource.name));

        const resultBooleanServices = resultConfig.data.message === 'Ativado' ? true : false;

        localStorage.setItem("plugin-tray-key", resultConfig.data.data.code);
        localStorage.setItem("plugin-tray-key2", resultConfig.data.data.url);
        localStorage.setItem("plugin-tray-key3", resultConfig.data.data.storeId);

        setKey(resultConfig.data.data.code);
        setKey2(resultConfig.data.data.url);
        setKey3(resultConfig.data.data.storeId);

        setCheckedServices(resultBooleanServices);

    }, [])



    const setValueKey = (event) => {


        console.log(event)

        event.preventDefault();
        setKey(event.detail.value)


    }
    const setValueKey2 = (event) => {

        event.preventDefault();
        setKey2(event.detail.value)


    }

    const setValueKey3 = (event) => {

        event.preventDefault();
        setKey3(event.detail.value)


    }
    const setValueKeyLocalStorage = async () => {

        if (!key) {
            showMessage('O campo Code  precisa ser preenchido!', 'danger', 'Notice!');
            return;
        }

        if (!key2) {
            showMessage('O campo URL precisa ser preenchido!', 'danger', 'Notice!');
            return;
        }

        if (!key3) {
            showMessage('O campo Id da loja precisa ser preenchido!', 'danger', 'Notice!');
            return;
        }
        localStorage.setItem("plugin-tray-key", key);
        localStorage.setItem("plugin-tray-key2", key2);
        localStorage.setItem("plugin-tray-key3", key3);

        showMessage('Informação gravada com sucesso!', 'success', 'Notice!');
    }

    const handleOpenModal = () => {
        setShowModal(true)
    }
    const deleteServiceTray = async () => {

     const {status} = await deleteConfigService(company);

     if(status === 200){

         showMessage('Configuração excluida com sucesso!', 'success', 'Notice!');
         setTimeout(() => {
             setShowModal(false);
             window.location.reload();
         }, 2000);

         return
     }

     showMessage('Erro ao excluir a configuração!', 'danger', 'Notice!');

    }
    const handleCloseModal = () => {

        setShowModal(false);

    }

    const showMessage = (msg, type) => {

        setTimeout(() => {
            showToast({ type: type, message: msg })
        }, 100);

    }

    return (
        <>
            <div className="container w-100" >
                <div className="row" style={{ display: 'flex', justifyContent: 'space-around' }}>
                    <div className="col-3">

                    </div>

                    <div className="col-6">

                        <label>Code:</label>
                        <BdsInputPassword icon="data-security" label="Digite sua chave code" placeholder="Ex: 90391f2ef2220f8bf77fbea9ca..." onBdsChange={(event) => setValueKey(event)} value={key} />
                        <br></br>

                        <label>Url:</label>
                        <BdsInput icon="site" label="Digite sua url de API" placeholder="Ex: https://www.sualoja.com.br/web_api" onBdsChange={(event) => setValueKey2(event)} value={key2} />
                        <br></br>
                        <label>Id da loja:</label>
                        <BdsInput icon="company" label="Digite o id da sua loja" placeholder="Ex: 2345" onBdsChange={(event) => setValueKey3(event)} value={key3} />
                        <br></br>

                        {checkedServices && <bds-button icon="attention" variant="delete" style={{ display: 'flex', justifyContent: 'end' }} size="short" onClick={handleOpenModal} >Excluir Serviço</bds-button>}

                        {!checkedServices && <bds-button icon="save-disk" style={{ display: 'flex', justifyContent: 'end' }} size="short" onClick={setValueKeyLocalStorage} >Salvar Configuração</bds-button>}

                    </div>

                    <div className="col-3" >
                        <label>Status do Serviço:</label><br></br>
                        <Switch
                            checked={checkedServices} onColor="#3f7de8" />


                    </div>

                </div>
                <br></br>

            </div>



            <bds-modal open={showModal} close-button={false} size="dynamic">
                <div style={{ display: 'flex', alignItems: 'center', width: '600px' }}>
                 
                        <div>
        
                            <img  style={{ width:'300px'}} src="https://pontoparse.blip.ai/fonts/robotDelete.svg?69841fd994771b5c93051da8b0699dc7" />
                        </div>
                        <div>
                            <bds-typo variant="fs-20" bold="semi-bold">
                                Atenção!
                            </bds-typo>
                            <bds-typo variant="fs-14" bold="regular">
                                Você está removendo o serviço de mensagem ativas.
                                Esta ação não pode ser desfeita!
                            </bds-typo>
                        
                        </div>
                  
                    <div>
                     
                    </div>
                    </div>
                    <bds-modal-action>
                        <bds-button variant='secondary'  style={{left:'14px'}} onClick={handleCloseModal}>Voltar</bds-button>
                        <bds-button variant="delete" onClick={deleteServiceTray}>Ok, entendi</bds-button>
                    </bds-modal-action>
            </bds-modal>
        </>
    )

};

export default Config;
