import React, { useState, useEffect } from 'react';
import { BdsSelect, BdsInputPassword } from 'blip-ds/dist/blip-ds-react';
import Select from '../Select/index'
import getMessageTemplate from '../../services/getMessageTemplate'
import guid from '../../utils/guid';
import { showToast } from "../../api/commomService";
import getNameSpaceWhatsApp from '../../services/getNameSpaceWhatsApp';
import getStateBot from '../../services/getStateBot';
import getTokenTray from '../../services/getTokenTray';
import getStatuses from '../../services/getOrderStatudes';
import { consumer_key, consumer_secret } from '../../constants/application-actions';
import setServiceTray from '../../services/setServiceTray';
import configService from '../../services/getServiceTray';
import axios from 'axios';


const SendNotification = ({ shortName, accessKey1, email }) => {


    const [config, setConfig] = useState({ value: "No message selected", config: "" });
    const [data, setData] = useState([]);
    const [dataSubBot, setDataSubBot] = useState([]);
    const [dataLanguage, Language] = useState([{ label: 'Português', value: 'pt_BR' }, { label: 'Inglês', value: 'en_US' }]);
    const [message, setMessage] = useState([])
    const [template, setTemplate] = useState('');
    const [stateId, setStateId] = useState('');
    const [flowId, setFlowId] = useState('');
    const [filterLanguage, setFilterLanguage] = useState('');
    const [inputList, setInputList] = useState([{ state: "", statusName: "", template: "", variables: [], }]);
    const [loading, setLoading] = useState(false);
    const [phone, setPhone] = useState('');
    const [templateName, setTemplateName] = useState('Select a template');
    const [language, setLanguage] = useState('Select a language');
    const [subBots, setSubBots] = useState(JSON.parse(localStorage.getItem('details')));
    const [templateSubBot, setTemplateSubBot] = useState('Select a template');
    const [identity, setIdentity] = useState('');
    const [dataFlowId, setDataFlowId] = useState([{ label: "No message selected", value: "dfdd" }]);
    const [templateSubBotFlowId, setTemplateSubBotFlowId] = useState('Select a state');
    const [inputValueList, setInputValueList] = useState("");
    const [showStatus, setShowStatus] = useState(false);
    const [statusTray, setStatusTray] = useState(false);
    const [dataVarieble, setDataVariable] = useState([]);
    const [variableLength, setVariableLength] = useState("");
    const [listMessageTemplate, setListMessageTemplate] = useState([]);
    const [tokenTrayAdm, setTokenTrayAdm] = useState('');
    const [showModalExample, setShowModalExample] = useState(false)
    const [showModalSendMessage, setShowModalSendMessage] =useState(false)

    useEffect(() => {
        fnAuxTemplates()

    }, []);


    useEffect(() => {

        fnAuxTemplates()

    }, [filterLanguage]);

    function removerCaracteresEspeciais(string) {
        // Remove caracteres especiais usando expressão regular
        var stringSemCaracteresEspeciais = string.replace(/[^\w\s]/gi, '');
        
        // Converte a string para caixa baixa
        var stringEmCaixaBaixa = stringSemCaracteresEspeciais.toLowerCase();
        
        return stringEmCaixaBaixa;
      }
      


    const fnAuxTemplates = async () => {

        const arrayTemplates = [];

        const response = await getMessageTemplate(guid(), localStorage.getItem('info'));
        const resultNameSpace = await getNameSpaceWhatsApp(guid(), localStorage.getItem('info'));

        const resultConfig = await configService(removerCaracteresEspeciais(resultNameSpace.data?.resource?.name));
        setListMessageTemplate(resultConfig.data.data.messageTemplate || []);
        setTokenTrayAdm(resultConfig?.data?.data?.tokenCart || false);


        const templates = response.data.resource.data.filter((t) => t.name && t.language === filterLanguage && t.name.split('_')[0] !== 'sample' && t.status === 'APPROVED');

        templates.map((template) => {
            arrayTemplates.push({ id: template.name, label: template.name, value: template.name, selected: false })
        });

        setData(arrayTemplates);
    }

    const handleSendMessage = async () => {


        if (!tokenTrayAdm) {
            showMessage('O token ainda não foi gerado, faça o login novamente ou atualize a pagina da tray', 'info');
            return
        }

        if (!filterLanguage) {
            showMessage('O campo idioma precisa ser preenchido!', 'info');
            return
        }

        if (!template) {
            showMessage('O campo template deve ser preenchido', 'info');
            return
        }


        const payload = {

            template: template,
            company: localStorage.getItem('tenantId')

        }

        const endpoint = 'https://pontoparse.herokuapp.com/api/v1/api-parse-tray/carts';

        try {

            const result = await axios.post(endpoint, payload, {
                headers: {
                    'Content-Type': 'application/json',
                    'Authorization': 'Bearer ' + tokenTrayAdm
                }
            })

            if (result.status === 200) {

                if (result.data.status === false) {
                    showMessage(result.data.message, "info")
                    return
                }

                showMessage(result.data.message, "success");
                return
            }
        } catch (er) {
            showMessage("Erro ao processar a requisição", "danger")
        }

    }

    const showMessage = (msg, type) => {

        showToast({
            type: type,
            message: msg
        })
    }


    const handleInputChange = (e) => {

        setTemplate(e)
        setShowStatus(true)
    };

    const handleSetEventLanguage = (event) => {

        setFilterLanguage(event);
        setLanguage(event);

    }

    return (
        <>
            {/* <div style={{ display: 'flex', justifyContent: 'center' }}>
                <div ><br></br>
                    <label style={{ fontSize: '18px' }}>Selecione o template para recuperar o carrinho!</label>
                    <hr></hr>
                </div>
                <br></br>
            </div> */}
            <div className="container w-100">
                <div className="row" style={{ display: 'flex', justifyContent: 'center' }}>

                    <div className="col-9">
                        <label>Token de login Tray: </label>
                        <BdsInputPassword label="Token Tray" readonly={true} value={tokenTrayAdm}></BdsInputPassword>

                    </div>


                </div>
                <div className="row" style={{ display: 'flex', justifyContent: 'center' }}>

                    <div className="col-3">
                        <label>Idoma: </label>
                        <Select
                            icon="search"
                            name="template"
                            label="Selecione um idioma"
                            placeholder="Selecione"
                            multiple={false}
                            searchable={true}
                            options={dataLanguage}
                            selected={dataLanguage}
                            onChange={($event) => handleSetEventLanguage($event)}
                        />
                    </div>

                    <div className="col-6">
                        <div className="col-3">
                            <label>Template:</label>
                        </div>
                        <Select
                            icon="filter"
                            name="template"
                            label="Selecione um modelo de mensagem"
                            placeholder="Selecione"
                            multiple={false}
                            searchable={true}
                            options={data}
                            selected={data}
                            onChange={($event) => handleInputChange($event)}
                        />
                    </div>
                    <hr />
                    <div className="container w-100" >
                        <hr />
                        <div style={{ display: 'flex', justifyContent: 'space-between' }}>

                            <div style={{ marginLeft: '100px' }}>
                                <span><strong>1:</strong> Para utilizar esse recurso, você precisa instalar a extensão abaixo e efeutar um login em sua conta na tray!</span><br />
                                <span ng-if="tab.tabHref">
                                    <a href="https://chrome.google.com/webstore/detail/plugin-desk/bmjjlaomahkobdcmgnbihomlipaoolnh?hl=pt-BR" class="tab-href" target="_blank" style={{ color: 'black' }}>Clique aqui! <i class="icon-external-link icon icon-xss mb2"></i></a>
                                </span><br /><br />
                                <span><strong>2:</strong> Ao criar um template/ modelo de mensagem, ele precisar ter 2 variáveis, são elas:</span><br>
                                </br>
                                <span><strong>2.a:</strong> Nome do cliente.</span><br></br>
                                <span><strong>2.b:</strong> Url do carrinho.</span><br /><br />
                                <span><strong>3:</strong> Um evento personalizado com nome de "Tray - Carrinho - Abandonado" será criado automaticamente.</span><br /><br />


                            </div>
                        </div>
                        <hr />
                    </div>

                    <div style={{ display: 'flex', justifyContent: 'space-between' }}>
                        <bds-button icon="external-file" style={{ width: '100%' }} size="short" onClick={()=> {setShowModalSendMessage(true)}} >Recuperar carrinho</bds-button>
                        <bds-button icon="info" variant="secundary" style={{ width: '45%' }} size="short" onClick={() => setShowModalExample(true)} >Exemplo</bds-button>
                    </div>
                </div>
            </div>

            <bds-alert open={showModalExample}>
                <bds-alert-header variant="system" icon="info">
                    Exemplo de template
                </bds-alert-header>
                <bds-alert-body>
                    Olá <strong>"Variavel 1"</strong>, como vai?<br></br><br></br>

                    Percebi que você deixou alguns itens no seu carrinho de compras. <br></br>
                    Que tal voltar lá e concluir a compra? Tenho certeza de que você vai adorar finalizar essa experiência!<br></br><br></br>
                    
                    <strong>"Variavel 2"</strong>
                     
                </bds-alert-body>
                <hr></hr>
                <bds-alert-actions>
                    <bds-button variant="secondary" bold="true" onClick={() => {setShowModalExample(false)}}>Entendi</bds-button>
                </bds-alert-actions>
            </bds-alert>

            <bds-alert open={showModalSendMessage}>
                <bds-alert-header variant="system" icon="info">
                   Confirmação de envio!
                </bds-alert-header>
                <bds-alert-body>
                    Você realmente deseja executar essa operação?
                </bds-alert-body>
                <hr></hr>
                <bds-alert-actions>
                   <bds-button icon="send" variant="secondary" bold="true" onClick={handleSendMessage}>Sim</bds-button>
                    <bds-button variant="secondary" bold="true" onClick={() => {setShowModalSendMessage(false)}}>Não</bds-button>
                </bds-alert-actions>
            </bds-alert>
        </>


    )

};

export default SendNotification;